<script>
  import Modal from '../modal/Modal.svelte'
  
  let webClientTest

  if(/Android|webOS|iPhone|iPad|iPod|BlackBerry|IEMobile|Opera Mini/i.test(navigator.userAgent)){
    webClientTest = {
      phone: true,
      desktop: false
    }
  }else{
    webClientTest = {
      phone: false,
      desktop: true
    }
  }

  function openModal() {
    console.log('test')
    let event = new CustomEvent('open-modal', {detail: {title: 'Menu'}})
    document.dispatchEvent(event)
  }

</script>

<div class="user-avatar__component">
  {#if webClientTest.phone}
  <a href="#!" on:click|preventDefault={ openModal }>
    <figure class="avatar avatar-lg badge" data-badge="12">
      <img src="/assets/avatar.png" alt="">
      <i class="avatar-presence online"></i>
    </figure>
  </a>
  {:else if webClientTest.desktop}
  <figure class="avatar avatar-lg badge" data-badge="12">
    <img src="/assets/avatar.png" alt="">
    <i class="avatar-presence online"></i>
  </figure>
  {/if}

  <Modal />
</div>

<style>
  .user-avatar__component {
    display: inline-block;
  }

</style>

